import React, { Component } from "react";
import { Link } from "gatsby";
import Layout from "../../components/layout/index";
import SEO from "../../components/seo";
import { SectionBox } from "../../components/home";
import Icon from "../../components/Icon";
import mixpanel from "../../lib/mixpanel/index";
import "../../layouts/index.css";
import styleVars from "../../styles/style-vars";
import { shareAthleteStory } from "../../lib/utils";
import Loading from "../../components/Loading";

const PaymentStatus = (props) => {
  var searchParams = new URLSearchParams(
    typeof window !== "undefined" ? window.location.search : ""
  );

  const status = (searchParams.get("status") || "").toLocaleLowerCase();
  const paymentId = searchParams.get("payment_id");
  let paymentAmount = searchParams.get("payment_amount");
  paymentAmount = (Number(paymentAmount) || 0) / 100;
  // paymentAmount in 2 decimal places
  paymentAmount = paymentAmount.toFixed(2);
  let feeAmount = searchParams.get("surcharge_amount");
  feeAmount = (Number(feeAmount) || 0) / 100;
  // feeAmount in 2 decimal places
  feeAmount = feeAmount.toFixed(2);

  return (
    <Layout>
      <SEO title="Payment Status" />
      <div
        className="main-content"
        style={{
          minHeight: "calc(100vh - 100px)",
        }}
      >
        {status === "" ? (
          <div style={{
            marginTop: 100,
          }}>
            <Loading />
          </div>
        ) : (
          <div>
            <div className={`${styleVars.page.xl} mb10`}>
              <div className="pt10 pb10 pt20-ns tc tl-ns pb20-ns">
                <div>
                  {status === "success" ? (
                    <Icon name="checked.png" className="ma0 pa0 mh12 cp" />
                  ) : (
                    <Icon name="cancel.png" className="ma0 pa0 mh12 cp" />
                  )}
                </div>
                <h1 className="pa0 f3 f1-ns center fw-bold tc">
                  {status === "success"
                    ? "Payment Successful"
                    : "Payment Failed"}
                </h1>
                <div
                  style={{
                    maxWidth: "400px",
                    margin: "0 auto",
                  }}
                >
                  {
                    // if payment failed then show the error message
                    status !== "success" ? (
                      <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal">
                        NOTE: If you have been charged, the amount will be
                        refunded to your account within 7 working days.
                        <br />
                        You can take a screenshot of this page for your
                        reference.
                        <br />
                        Please WhatsApp us at{" "}
                        <a
                          href="https://wa.me/918431624370"
                          target="_blank"
                          className="primary-text-color"
                        >
                          +91 8431624370
                        </a>{" "}
                        if you have any questions.
                        <br />
                        <br />
                      </p>
                    ) : (
                      <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal">
                        Thank you for your generous contribution, your support
                        means the world to us and the athletes we empower.
                        <br />
                        <br />
                      </p>
                    )
                  }
                  <div className="status-table-item">
                    <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal primary-text-color">
                      Payment ID
                    </p>
                    <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal">
                      {paymentId || "-"}
                    </p>
                  </div>
                  <div className="status-table-item">
                    <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal primary-text-color">
                      Payment Amount
                    </p>
                    <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal">
                      ₹ {paymentAmount || 0}
                    </p>
                  </div>
                  <div className="status-table-item">
                    <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal primary-text-color">
                      Gateway Fees
                    </p>
                    <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal">
                      ₹ {feeAmount || 0}
                    </p>
                  </div>
                </div>
              </div>
            </div>
            {
              // if payment success then show share button
              status === "success" ? (
                <div className="flex justify-center">
                  <div className="flex flex-column flex-row-ns align-center-m">
                    <button
                      className="btn-base btn-outlined f5"
                      style={{
                        minWidth: 300,
                        height: 60,
                      }}
                      onClick={() => shareAthleteStory()}
                    >
                      Share The Story With Friends
                    </button>
                  </div>
                </div>
              ) : null
            }
          </div>
        )}
      </div>
    </Layout>
  );
};
export default PaymentStatus;
